.footer {
  padding: 60px 20px 20px;
  background-color: $background-color;

  &__wrap {

    @include onTablet {
      display: flex;
      justify-content: space-between;
    }

    .contacts {
      &-contact {
        font-family: "Playfair Display", sans-serif;
        font-size: 18px;
        font-weight: 700;
        line-height: 27px;
        color: $white-color;
        padding-bottom: 10px;
      }

      &-addres {
        font-family: "Raleway", sans-serif;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: $white-color;
        padding-bottom: 32px;
      }

      &__media {
        display: flex;

        &-photo {
          padding-right: 25px;
          margin-bottom: 52px;

          &:last-child {
            padding-right: 0;
          }
        }
      }

      &__days {
        &-work {
          font-family: "Playfair Display", sans-serif;
          font-size: 18px;
          font-weight: 700;
          line-height: 27px;
          color: $white-color;
          padding-bottom: 10px;
        }

        &-day {
          font-family: "Raleway", sans-serif;
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          color: $white-color;
          padding-bottom: 50px;
        }
      }

      &__menu {
        &-title {
          font-family: "Playfair Display", sans-serif;
          font-size: 20px;
          font-weight: 700;
          line-height: 30px;
          color: $white-color;
        }

        &-exhibition,
        &-events,
        &-news {
          font-family: "Raleway", sans-serif;
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          color: $white-color;

          &:last-child {
            padding-bottom: 60px;
          }
        }
      }
    }
  }
  &__terms {
    display: flex;
    justify-content: space-between;

    @include onTablet {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    &-policy {
      font-family: "Raleway", sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: $white-color;

      @include onTablet {
        padding-right: 20px;
      }
    }
  }
}
